/*eslint no-console:0, no-process-env:0 */

import debug from 'bows';
import merge from 'lodash.merge';
import { objectAssign } from '@nrk/ludo-ponyfills';

import {
  spinner,
  poster,
  controller,
  fixedSubtitles,
  defaultControllers,
  notificationCenter,
  LudoLangNbNo,
  navKeys,
  LudoUI
} from '@nrk/ludo-ui';

import {
  ludo,
  LudoMediaSyncPlayerAdapter,
  LudoMediaSyncHLSPlayerAdapter,
  LudoErrorMessageHandler,
} from '@nrk/ludo-core';

import {
  nrkMediaItemLoader,
  nrkApiClient
} from '@nrk/ludo-nrk-psapi-client';


const log = debug('ludo:embed');

const getDefaultOptions = () => ({
  autoplay: false,
  autoadvance: false,
  nativeControls: false,
  controls: true,
  loop: true,
  mute: false,
  thumbnails: true,
  adapter: null, // preferred adapter
  apiBaseUrl: 'psapi.nrk.no',
  apiAccept: 'application/vnd.nrk.psapi+json; version=9; ludo-client=true',
  gaTrackingId: 'UA-55865429-2',
  userId: null,
  DEFAULT_LIVE_OFFSET: 15,
  LIVE_DELAY_BETWEEN_TIMESTAMP_AND_STREAM: 3,
  SUPPORTS_DVR: true,
  versions: process.env.versions,
  version: process.env.version
});

/**
 * Mount player into web page, given an element to mount to
 * @param {HTMLElement} ludoElement Mount point
 * @param {String|Array|Object[]} programIds Program IDs
 * @param {Object} options Player options
 * @returns {Object} player
 */
function mount(ludoElement, programIds, options = {}) {
  const initialPoster = ludoElement.querySelector('img');
  const initialPlayer = ludoElement.querySelector('video,audio');

  const { 'pinned-control-overlay': isPinned } = options;

  const ui = LudoUI(ludoElement, { // eslint-disable-line new-cap
      'pinned-control-overlay': isPinned,
      lang: LudoLangNbNo,
      version: process.env.version
    }),
    ludoPoster = poster(ui);

  const
    posterEl = ludoElement.querySelector('.ludo-poster');

  if (initialPlayer) {
    ludoElement.insertBefore(initialPlayer, ludoElement.firstChild);
  }

  if (initialPoster && initialPlayer) {
    initialPlayer.poster = initialPoster.src;
  }

  if (initialPoster) {
    posterEl.querySelector('img').src = initialPoster.src;
    initialPoster.parentElement.removeChild(initialPoster);
  }

  const mergedOptions = merge({}, getDefaultOptions(), options);

  const ludoOptions = objectAssign({}, {
    stageElement: ludoElement,
    posterElement: posterEl,
    use: [
      spinner(ui),
      controller(ui),
      defaultControllers(ui),
      notificationCenter(ui),
      navKeys(ui),
      LudoErrorMessageHandler,
      fixedSubtitles(ui)
    ]
  }, mergedOptions);

  let adapters;
  if (options.adapters) {
    adapters = [ ...options.adapters ];
  } else {
    const { timingObject } = options;
    adapters = [
      new LudoMediaSyncHLSPlayerAdapter({ timingObject }),
      new LudoMediaSyncPlayerAdapter({ timingObject })
    ];
  }

  function getUrlDomain (url) {
    return url.replace(/(^https?:\/\/|\/.*?$)/g, '');
  }

  const apiClient = nrkApiClient({
    domain: getUrlDomain(mergedOptions.apiBaseUrl),
    headers: {
      Accept: mergedOptions.apiAccept
    }
  });

  const debug = mergedOptions.debug || false;
  if (debug && log.enableBrowserConsole) {
    log.enableBrowserConsole();
  }

  log('Using options', ludoOptions);

  const player = ludo({
    loaders: [
      nrkMediaItemLoader(apiClient)
    ],
    preparators: [
      ludoPoster
    ],
    adapters
  })(programIds, ludoOptions);

  return player;
}

module.exports = mount;



